* {
  font-family: Arial, Helvetica, sans-serif;
  font-size: 14px;
}

body {
  background: #4a4a4a;
}

.container {
  width: 900px;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: baseline;
  margin: 0 auto;
  display: flex;
}

.cg-dropdown {
  min-width: 235px;
  border: none;
  border-radius: 10px;
  margin: 0;
  padding: 0;
  position: relative;
}

.cg-select {
  min-height: 50px;
  color: #fff;
  text-overflow: ellipsis;
  cursor: pointer;
  background: #2a2f3b;
  border: none;
  border-radius: 5px;
  justify-content: space-between;
  align-items: center;
  padding: 5px;
  transition: all .5s;
  display: flex;
}

.cg-select .selected {
  max-width: 195px;
  margin: 5px;
}

.cg-select:hover {
  background: #394050;
  transition: all .5s;
}

.caret {
  width: 0;
  height: 0;
  border-top: 6px solid #fff;
  border-left: 5px solid #0000;
  border-right: 5px solid #0000;
  margin-right: 10px;
  transition: all .5s;
}

.caret_rotate {
  transition: all .5s;
  transform: rotate(180deg);
}

.list {
  max-height: 230px;
  width: 220px;
  color: #fff;
  opacity: 0;
  z-index: 1;
  background: #2a2f3b;
  border: 1px solid #0a0b0e;
  border-radius: 5px;
  margin-top: -.2px;
  padding: 7px;
  list-style: none;
  transition: all .5s;
  display: none;
  position: absolute;
  overflow-y: auto;
  box-shadow: 2px 3px 5px #00000080;
}

.list__item {
  flex-direction: row;
  align-items: center;
  padding: 15px;
  transition: all .5s;
  display: flex;
}

.list__item:hover {
  cursor: pointer;
  background: #8282822c;
  transition: all .5s;
}

.multiselect-tag {
  flex-flow: wrap;
  align-items: center;
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
}

.multiselect-tag li {
  height: 20px;
  background: #76767659;
  border-radius: 5px;
  align-items: center;
  margin: 0 5px 5px 0;
  padding: 2px 4px;
  display: flex;
  position: relative;
}

.multiselect-tag__button {
  width: 15px;
  height: 16px;
  border: none;
}

.category {
  height: 20px;
  border-bottom: 1px solid;
  align-items: center;
  margin: 6px 5px 0 7px;
  font-size: 17px;
  display: flex;
}

input[type="checkbox"] {
  cursor: pointer;
  margin: 0 5px 0 0;
}

.svg-icon {
  width: 16px;
  cursor: pointer;
  background-color: #bebebc;
  border-radius: 5px;
  margin-left: 3px;
  transition: all 1s;
}

.svg-icon:hover {
  background-color: #fff;
  transition: all 1s;
}

.svg-icon path {
  color: #000;
  stroke: currentColor;
  stroke-width: .5px;
  transition: all .2s;
}

.active {
  background: #8282822c;
}

.open {
  opacity: 1;
  transition: all .5s;
  display: block;
}

.open_none {
  opacity: 0 !important;
}

.disabled {
  background-color: #8f9195 !important;
}

.overflow-hidden {
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}

::-webkit-scrollbar {
  width: 10px;
}

::-webkit-scrollbar-track {
  background-color: #d1d1d19d;
}

::-webkit-scrollbar-thumb {
  background-color: #4d4d4d;
}

